@import url("https://fonts.googleapis.com/css2?family=Kalam:wght@700&family=Merriweather:wght@700&family=Playfair+Display:wght@500&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.home-menu {
  transition: 0.2s;
}

.home-menu:hover {
  transform: scale(1.02);
  box-shadow: 0px 0px 10px 0px black;
  border-radius: 20px;
}

.dark .home-menu:hover {
  transform: scale(1.02);
  box-shadow: 0px 0px 10px 0px rgb(6, 116, 220);
  border-radius: 20px;
}
.thead {
  padding: 10px;
}

@layer utilities {
  .w-29 {
    width: 31%;
  }
}

.selected {
  color: black;
}

